export default (sequelize, Sequelize) => {
  const File = sequelize.define('file', {
    type: {
      type: Sequelize.STRING,
      required: true,
    },
    name: {
      type: Sequelize.STRING,
      required: true,
    },
    path: {
      type: Sequelize.STRING,
      required: true,
    },
    activeFlag: {
      type: Sequelize.BOOLEAN,
      defaultValue: true,
    }
  })
  return File
}